import * as React from "react"

/**
 * Render a React component with css included in a sibling <style> tag.
 * @beta
 */
export const withCSS = <T extends object>(Component: React.ComponentType<T>, escapedCSS: string) => (props: T) => {
    return (
        <>
            <Component {...props} />
            <style
                dangerouslySetInnerHTML={{
                    __html: escapedCSS,
                }}
            />
        </>
    )
}
