import type { Transition } from "framer-motion"
import { isEqual } from "../render/utils/isEqual"
import * as React from "react"

function gestureVariant(variant: string, type: "hover" | "pressed") {
    return `${variant}-${type}`
}

function nextVariant(allVariants: string[], currentVariant: string): string {
    const index = allVariants.indexOf(currentVariant)
    let nextIndex = index + 1
    if (nextIndex >= allVariants.length) {
        nextIndex = 0
    }
    return allVariants[nextIndex]
}

type UnknownProps = Record<string, unknown>
/**
 * Variant / Node Id / React Prop / Val
 */
type VariantProps = Record<string, Record<string, UnknownProps>>

type GestureState = Partial<{
    isHovered: boolean
    isPressed: boolean
}>

// EnabledGestures and EnabledVariantGestures are mirrored from Framer in VariantsStore.ts
interface EnabledGestures {
    hover: boolean
    pressed: boolean
}

type EnabledVariantGestures = Record<string, EnabledGestures>
type VariantNames = string[]
interface VariantState {
    variants: VariantNames
    classNames: string
    transition: Partial<Transition> | undefined
    addVariantProps: (elementId: string) => UnknownProps
    setVariant: (variant: string) => void
    setGestureState: (gestureState: GestureState) => void
}

/**
 * @internal
 */
export const enum VariantSelector {
    Variant = "v",
    Component = "c",
}

function createClassNames(
    internalState: InternalState,
    enabledGestures: EnabledVariantGestures | undefined,
    defaultVariant: string
) {
    const { baseVariant, isHovered, isPressed } = internalState
    const rootClass = `framer-${VariantSelector.Component}-${defaultVariant}`

    if (!baseVariant) return rootClass

    const classNames = [rootClass, `framer-${VariantSelector.Variant}-${baseVariant}`]

    if (enabledGestures) {
        const { hover, pressed } = enabledGestures[baseVariant] || {}

        if (pressed && isPressed) {
            classNames.push("pressed")
        } else if (hover && isHovered) {
            classNames.push("hover")
        }
    }

    return classNames.join(" ")
}

interface InternalState {
    isHovered: boolean
    isPressed: boolean
    baseVariant: string | undefined
    activeVariants: VariantNames
}

/**
 * Flag setVariantState as cycling variants.
 * @beta
 */
export const CycleVariantState = Symbol("cycle")

/**
 * Handle stateful logic in Framer Canvas Components.
 *
 * @beta
 */
export function useVariantState({
    defaultVariant,
    variant,
    transitions,
    variantProps,
    enabledGestures,
    cycleOrder = [],
}: {
    defaultVariant: string
    cycleOrder: string[]
    variant?: string
    transitions?: Record<string, Partial<Transition>>
    variantProps?: VariantProps
    enabledGestures?: EnabledVariantGestures
}): VariantState {
    const [forceRenderCount, forceRender] = React.useState<number>(0)

    const internalState = React.useRef<InternalState>({
        isHovered: false,
        isPressed: false,
        baseVariant: variant,
        activeVariants: variant ? [variant] : [],
    })

    const resolveGestureState = React.useCallback(() => {
        const { baseVariant = defaultVariant, isPressed, isHovered } = internalState.current
        const nextVariants: VariantNames = []

        // The base variant is simply the element's `style` so we don't set it.
        if (baseVariant !== defaultVariant) nextVariants.push(baseVariant)

        const gestures = enabledGestures?.[baseVariant]
        if (gestures) {
            if (isPressed && gestures.pressed) {
                nextVariants.push(gestureVariant(baseVariant, "pressed"))
            } else if (isHovered && gestures.hover) {
                nextVariants.push(gestureVariant(baseVariant, "hover"))
            }
        }

        // Only force a render if the new active variants have changed.
        if (!isEqual(internalState.current.activeVariants, nextVariants, true)) {
            internalState.current.activeVariants = nextVariants
            forceRender(v => v + 1)
        }
    }, [enabledGestures, defaultVariant])

    const setGestureState: VariantState["setGestureState"] = React.useCallback(
        ({ isHovered, isPressed }) => {
            if (isHovered !== undefined) internalState.current.isHovered = isHovered
            if (isPressed !== undefined) internalState.current.isPressed = isPressed

            resolveGestureState()
        },
        [resolveGestureState]
    )

    const setVariant = React.useCallback(
        (proposedVariant: typeof CycleVariantState | string) => {
            if (proposedVariant === CycleVariantState) {
                internalState.current.baseVariant = nextVariant(
                    cycleOrder,
                    internalState.current.baseVariant || defaultVariant
                )
            } else {
                internalState.current.baseVariant = proposedVariant
            }

            resolveGestureState()
        },
        [resolveGestureState, cycleOrder, defaultVariant]
    )
    /**
     * @TODO
     * This is not production ready. `variantProps` can include React props as
     * values. Those props can change when the variant doesn't change, and this
     * callback may not be memoized correctly to account for that. One approach
     * might be to use `useMemo` on `variantProps` in the GeneratedComponent
     * template.
     */
    const addVariantProps: VariantState["addVariantProps"] = React.useCallback(
        id => {
            if (!variantProps) return {}
            const { activeVariants } = internalState.current

            if (!activeVariants.length) return {}
            const props = {}

            // Create an object with all valid props for the target node. Values
            // in higher priority variants override same values in lower
            // priority variants.
            for (const activeVariant of activeVariants) Object.assign(props, variantProps[activeVariant]?.[id])

            return props
        },

        [variantProps]
    )

    React.useEffect(() => {
        if (variant !== internalState.current.baseVariant) setVariant(variant || defaultVariant)
    }, [variant, setVariant, defaultVariant])

    const transition = React.useMemo(() => {
        if (!transitions) return undefined

        if (internalState.current.baseVariant) {
            const variantTransition = transitions[internalState.current.baseVariant]
            if (variantTransition) return variantTransition
        }

        return transitions.default

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [transitions, forceRenderCount]) // We need to invalidate this memo whenever the internal state of this function changes.

    return {
        variants: internalState.current.activeVariants,
        transition,
        classNames: createClassNames(internalState.current, enabledGestures, defaultVariant),
        setVariant,
        setGestureState,
        addVariantProps,
    }
}
