import * as React from "react"
import type { Rect } from "../types/Rect"
import type { DeprecatedBackgroundProperties } from "../traits/Background"
import type { LayerProps } from "../presentation/Layer"
import { BackgroundImage } from "../types/BackgroundImage"
import { Animatable } from "../../animation/Animatable"
import { setImageForFill } from "../utils/imageForFill"

function applyForwardOverrides(
    background: BackgroundImage,
    props: Partial<DeprecatedBackgroundProperties & LayerProps>
): BackgroundImage {
    const { _forwardedOverrides, id } = props
    const src = _forwardedOverrides && id ? _forwardedOverrides[id] : undefined
    if (src && typeof src === "string") {
        background = { ...background, src }
    }
    return background
}

export function backgroundImageFromProps(
    props: Partial<DeprecatedBackgroundProperties & LayerProps>
): BackgroundImage | undefined {
    const background = Animatable.get(props.background, null)
    if (!BackgroundImage.isImageObject(background)) {
        return
    }
    return applyForwardOverrides(background, props)
}

/** @internal */
export function collectBackgroundImageFromProps(
    props: Partial<DeprecatedBackgroundProperties & LayerProps>,
    rect: Rect | null,
    style: React.CSSProperties
): void {
    const image = backgroundImageFromProps(props)
    if (image) setImageForFill(image, rect, style)
}
