import * as React from "react"
import { Size } from "../types/Size"
import { BackgroundImage, ImageFit } from "../types/BackgroundImage"
import { safeWindow } from "../../utils/safeWindow"
import { runtime } from "../../utils/runtimeInjection"
import { RenderTarget } from "../types/RenderEnvironment"
// isAssetReference must be imported as a relative path
// to prevent it from being included in the framer.api.md imports list as a module.
import { isAssetReference } from "../../../../Assets/src/assetReference"

/**
 * @internal
 */
export function imageUrlForAsset(asset: string, size?: number) {
    let containerSize: Size | null = null
    let intrinsicSize: number | undefined

    if (size !== undefined) {
        // make sure we ask for at least 1x1
        size = Math.max(1, size)
        containerSize = { width: size, height: size }
        intrinsicSize = size
    }

    const fakeImage: BackgroundImage = {
        src: "",
        intrinsicWidth: intrinsicSize,
        intrinsicHeight: intrinsicSize,
    }

    const assetSize = getAssetSize(fakeImage, containerSize)
    return _imageURL(asset, assetSize)
}

/**
 * @internal
 */
export function imageUrlForFill(image: BackgroundImage, containerSize: Size | null) {
    const size = getAssetSize(image, containerSize)
    return _imageURL(image.src, size)
}

const cachedImageURLs = new Set<string>()
function isImageCached(imageURL: string): boolean {
    if (cachedImageURLs.has(imageURL)) {
        return true
    }
    const imageElement = new Image()
    imageElement.src = imageURL
    // Because we check if the image element is complete without waiting for
    // loading, we know this image must already be in the cache
    const isCached = imageElement.complete
    if (isCached) {
        cachedImageURLs.add(imageURL)
    }
    return isCached
}

/**
 * @internal
 */
export function setImageForFill(image: BackgroundImage, containerSize: Size | null, style: React.CSSProperties) {
    const imageURL = imageUrlForFill(image, containerSize)

    // Quality options to select the smallest variant available to use as a fallback image
    const tinyRect = { x: 0, y: 0, width: 1, height: 1 }
    const fallbackImageURL = imageUrlForFill(image, tinyRect)

    let backgroundImage = `url("${imageURL}")`

    if (imageURL !== fallbackImageURL && !isImageCached(imageURL)) {
        backgroundImage += `, url("${fallbackImageURL}")`
    }

    style.backgroundImage = backgroundImage
    style.backgroundSize = cssBackgroundSize(image.fit)
    style.backgroundRepeat = "no-repeat"
    style.backgroundPosition = "center"
}

function cssBackgroundSize(imageFit: ImageFit | undefined) {
    switch (imageFit) {
        case "fit":
            return "contain"
        case "stretch":
            return "100% 100%"
        default:
            return "cover"
    }
}

/**
 * @internal
 */
export function getAssetSize(image: BackgroundImage, containerSize: Size | null) {
    const intrinsicWidth = image.intrinsicWidth ?? null
    const intrinsicHeight = image.intrinsicHeight ?? null

    const dpr = safeWindow.devicePixelRatio
    const bitmapMaxSize = Math.max(intrinsicWidth ?? 0, intrinsicHeight ?? 0)
    const canvasMaxSize = Math.max(containerSize?.width ?? 0, containerSize?.height ?? 0)

    let size: number | undefined
    const target = RenderTarget.current()
    const fullQuality = !containerSize || target === RenderTarget.export || target === RenderTarget.preview
    const noIntrinsicSize = intrinsicWidth === null || intrinsicHeight === null // No known size means we wont have resized versions
    if (fullQuality || noIntrinsicSize) {
        size = undefined
    } else {
        // We generate the following sizes
        // 512, 1024, 2048, 4096

        // For larger bitmaps, we can get away with less quality
        const canvasPixels = canvasMaxSize * dpr
        if (canvasPixels <= 512) size = 512
        else if (canvasPixels <= 1024) size = 1024
        else if (canvasPixels <= 2048) size = 2048
        else if (canvasPixels <= 4096) size = 4096
    }
    // We limit the image size to 4096 for now. Figma does the same actually.
    if (size === undefined && bitmapMaxSize * dpr > 4096) {
        size = 4096
    }
    return size
}

function _imageURL(asset: string | null, size?: number): string {
    if (asset === null) return ""
    if (/^\w+:/.test(asset) && !isAssetReference(asset)) return asset

    const resolvedAsset = runtime.assetResolver(asset, {
        size,
        isExport: RenderTarget.current() === RenderTarget.export,
    })

    return resolvedAsset || ""
}
