import * as React from "react"
import type { Ref } from "react"
import type { BackgroundImage } from "../types/BackgroundImage"
import { forwardRef } from "react"
import { MotionDivProps } from "./Frame/types"
import { BackgroundImageComponent } from "../style/BackgroundImageComponent"
import { motion, MotionStyle } from "framer-motion"
import { USE_IMAGE_ELEMENT_FOR_BACKGROUND } from "../style/wantsImageElementForBackground"
import { useBackgroundImage } from "../style/backgroundImage"

/** @internal */
interface BackgroundImageProps {
    background: BackgroundImage
}

/** @internal */
interface ImageProps extends MotionDivProps, BackgroundImageProps {}

/** @internal */
export const Image = forwardRef(function Image(props: Partial<ImageProps>, ref: Ref<HTMLDivElement>) {
    const { style: userStyle, background, children, ...rest } = props

    const backgroundStyle: React.CSSProperties = {}

    if (!USE_IMAGE_ELEMENT_FOR_BACKGROUND) {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        useBackgroundImage(background, null, backgroundStyle)
    }

    const style: MotionStyle = {}

    if (background) {
        // Strip existing `background` props from style, and use `...restStyle`
        // when we are rendering a background asset. This ensures that the
        // background prop can be properly reset when removing the
        // background-image.
        const { background: _, ...restStyle } = userStyle ?? {}
        // Assign the style to the backgroundStyle, because it needs to have
        // precedence for FrameWithMotion
        Object.assign(style, backgroundStyle, restStyle)
    } else if (userStyle) {
        Object.assign(style, userStyle)
    }

    return (
        <motion.div {...rest} style={style} ref={ref}>
            {USE_IMAGE_ELEMENT_FOR_BACKGROUND && background ? (
                <BackgroundImageComponent image={background} containerSize={null} />
            ) : null}

            {children}
        </motion.div>
    )
})
